<template>
  <div id="app">
    <notifications group="foo" position="bottom right"/>
    <Navbar/>
    <div class="container">
      <router-view/>
    </div>
    <b-modal no-close-on-backdrop id="modal-create" title="Create a new post" ok-only no-stacking>
      <form>
        <div class="form-group">
          <label>Region:</label>
          <select class="form-control" v-model="data.region">
            <option value="euw1">EUW</option>
            <option value="eun1">EUNE</option>
            <option value="na1">NA</option>
          </select>
        </div>
        <div class="form-group">
          <label for="recipient-name" class="col-form-label">Summoner name:</label>
          <input type="text" class="form-control" v-model="data.name" id="recipient-name">
        </div>
        <div class="form-group">
          <label for="exampleFormControlSelect1">Primary position:</label>
          <select class="form-control" id="exampleFormControlSelect1" v-model="data.primary">
            <option value="top" :disabled="data.secondary == 'top'">Top</option>
            <option value="jungle" :disabled="data.secondary == 'jungle'">Jungle</option>
            <option value="middle" :disabled="data.secondary == 'middle'">Middle</option>
            <option value="bottom" :disabled="data.secondary == 'bottom'">Bottom</option>
            <option value="support" :disabled="data.secondary == 'support'">Support</option>
          </select>
        </div>
        <div class="form-group">
          <label for="exampleFormControlSelect1">Secondary position:</label>
          <select class="form-control" id="exampleFormControlSelect1" v-model="data.secondary">
            <option value="top" :disabled="data.primary == 'top'">Top</option>
            <option value="jungle" :disabled="data.primary == 'jungle'">Jungle</option>
            <option value="middle" :disabled="data.primary == 'middle'">Middle</option>
            <option value="bottom" :disabled="data.primary == 'bottom'">Bottom</option>
            <option value="support" :disabled="data.primary == 'support'">Support</option>
          </select>
        </div>
        <div class="form-group">
          <label for="message-text" class="col-form-label">
            Bio:
            <span>*</span>
          </label>
          <textarea class="form-control" id="message-text" v-model="data.bio" maxlength="128"></textarea>
        </div>
      </form>
      <div slot="modal-footer">
        <button type="button" class="btn btn-primary" @click="postCreate()">Next</button>
      </div>
    </b-modal>

    <b-modal no-close-on-backdrop id="modal-verify" title="Verify your summoner" ok-only>
      <p>
        Open your League of Legends client. Go to settings. Select "Vertification" and enter this code:
        <span
          class="badge badge-danger"
        >{{my.code}}</span> then finally click save. Once verifications gets sent you can click verify below to complete verification.
      </p>
      <div slot="modal-footer">
        <button type="button" class="btn btn-primary" @click="postVerify()">Verify</button>
      </div>
    </b-modal>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar";
export default {
  data() {
    return {
      data: {
        primary: "",
        secondary: "",
        region: "",
        name: "",
        bio: ""
      },
      loading: {
        create: false,
        verify: false,
        fetch: false
      },
      my: {
        code: "",
        id: ""
      }
    };
  },
  methods: {
    async refresh() {
      this.$store.commit("update", []);
      const res = await this.$http.get(`http://localhost:8080/api/posts`);
      res.data.forEach(user => {
        if (user.summoner.region == "euw1") {
          user.summoner.region = "EUW";
        } else if (user.summoner.region == "eun1") {
          user.summoner.region = "EUNE";
        } else if (user.summoner.region == "na1") {
          user.summoner.region = "NA";
        }
      });
      this.$store.commit("update", res.data);
    },
    async postCreate() {
      if (
        this.data.name &&
        this.data.region &&
        this.data.bio &&
        this.data.primary &&
        this.data.secondary
      ) {
        const res = await this.$http.post("http://localhost:8080/api/posts", {
          name: this.data.name,
          region: this.data.region,
          bio: this.data.bio,
          primary: this.data.primary,
          secondary: this.data.secondary
        });

        if (!res.data.error) {
          this.my.id = res.data.summoner.id;
          this.my.code = res.data.code;
          this.$root.$emit("bv::show::modal", "modal-verify", "#btnShow");
        } else {
          this.$notify({
            type: "error",
            group: "foo",
            title: "Error:",
            text: res.data.error
          });
        }
      } else {
        this.$notify({
          type: "error",
          group: "foo",
          title: "Error:",
          text: "Complete all the fields"
        });
      }
    },
    async postVerify() {
      const res = await this.$http.post(
        "http://localhost:8080/api/posts/check",
        {
          id: this.my.id
        }
      );

      if (!res.data.error) {
        this.data.name = "";
        this.data.region = "";
        this.data.bio = "";
        this.data.primary = "";
        this.data.secondary = "";
        this.refresh();
        this.$root.$emit("bv::hide::modal", "modal-verify", "#btnShow");
      } else {
        this.$notify({
          type: "error",
          group: "foo",
          title: "Error:",
          text: res.data.error
        });
      }
    }
  },
  components: {
    Navbar
  }
};
</script>

<style>
.container {
  margin-top: 1rem;
}
</style>
